import { Component, OnInit } from '@angular/core';
import { SubTask } from 'src/app/commons/interfaces/aerc-interfaces/subtask.interface';
import { Task } from 'src/app/commons/interfaces/aerc-interfaces/task.interface';
import { AlumnoModel } from 'src/app/commons/models/aerc-models/alumno.model';
import { PreguntaRespuestaModel } from 'src/app/commons/models/aerc-models/pregunta-respuesta.model';
import { AuthenticationService } from '../../../../commons/services/authentication.service';
import { Router, ActivatedRoute } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { MatStepper } from '@angular/material/stepper';
import { UtilAlert } from '../../../../commons/util/util.alert';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { AppState } from 'src/app/store/app.reducer';
import { Store } from '@ngrx/store';
import { listarPreguntaSolicitud, unsetFormulario } from 'src/app/store/actions/aerc-actions/formulario.actions';
import { DocumentosModel } from 'src/app/commons/models/aerc-models/documentos-model';
import Swal from 'sweetalert2';
import { AlumnoSolicitudModel } from '../../../../commons/models/aerc-models/alumno-solicitud.model';
import { SolicitudService } from 'src/app/commons/services/aerc-services/solicitud.service';
import { SolicitudDetalleRespuestas } from 'src/app/commons/interfaces/aerc-interfaces/solicitud-detalle.interface';
import { SolicitudInnovacionDetalleService } from '../../../../commons/services/aerc-services/solicitud-innovacion-detalle.service';
import { SolicitudModel } from '../../../../commons/models/aerc-models/solicitud-model';
import { SolicitudDetalleService } from 'src/app/commons/services/aerc-services/solicitud-detalle.service';
import { saveAs } from 'file-saver';
import { AprobadorAercService } from '../../../../commons/services/aerc-services/aprobador-aerc.service';

@Component({
  selector: 'app-solicitud-academica',
  templateUrl: './solicitud-academica.component.html',
  styles: [
  ]
})
export class SolicitudAcademicaComponent implements OnInit {

  public nombreSolicitante: string = "";
  public habilitarFormulario: boolean = false;

  public solicitudDocumentacion: Array<DocumentosModel> = [];
  public preguntaRespuestasSolicitud: PreguntaRespuestaModel[];

  public formTipoSolicitud: FormGroup;
  public tipoSolicitud: number = 0;
  public nombreSolicitud: string = '';

  @BlockUI('stepperSolicitudAcademica') blockUI: NgBlockUI;

  public taskSolicitud: Task[] = [];
  public taskCompleted: boolean = false;

  public _subscriptionFormularioState: Subscription;
  public loading: boolean = false;

  public alumnoSolicitud = new AlumnoSolicitudModel;
  public puedeEnviarSolicitud = true;
  public tipoAprobador: number = 0;
  public tipoAprobadordesc: string = '';

  public isEditSolicitud: boolean = false;
  public accion: string = '';
  private idSolicitud: number = 0;
  private solicitud = new SolicitudModel;

  public botonTitle: string = 'Validar';

  constructor(
    private activatedRoute: ActivatedRoute,
    private store: Store<AppState>,
    private auth: AuthenticationService,
    private router: Router,
    private alert: UtilAlert,
    private solicitudService: SolicitudService,
    private detalleService: SolicitudDetalleService,
    private aprobadorService: AprobadorAercService

  ) {
    this.nombreSolicitante = this.auth.obtenerName();

    this.activatedRoute.params.subscribe(params => {
      // console.log(params['solicitud']);
      if ((params['solicitud'] != null && params['solicitud'] != '') && (params['accion'] != null && params['accion'] != '')) {
        this.isEditSolicitud = true;
        this.idSolicitud = params['solicitud'];
        this.accion = params['accion'];
        // console.log(this.accion);
        this.cargarSolicitudEdicion();
      }
    });
  }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------


  ngOnInit(): void {
    this.initFormTipoSolicitud();
    this._formularioState();
    this.findAprobador();
  }

  ngOnDestroy(): void {
    this._subscriptionFormularioState?.unsubscribe();
    this.store.dispatch(unsetFormulario());
  }



  //* STATES DEL REDUX
  //* ----------------------------------------------------------------------------------

  private _formularioState() {
    this._subscriptionFormularioState = this.store.select("formulario").subscribe((state) => {
      if (this.preguntaRespuestasSolicitud == null && state.listadoSolicitud != null && state.listadoSolicitud.length > 0) {
        this.preguntaRespuestasSolicitud = state.listadoSolicitud;
        this.crearFormularioTask(this.preguntaRespuestasSolicitud);
      }

      this.loading = state.loading;

      if (this.blockUI.isActive != state.loading) {
        state.loading ? this.blockUI.start("Iniciando Formulario...") : this.blockUI.stop();
      }
    })
  }

  //* INICIACION DEL FORMULARIO 
  //* ----------------------------------------------------------------------------------

  initFormTipoSolicitud() {
    this.formTipoSolicitud = new FormGroup({
      tipoSolicitud: new FormControl('', [Validators.required])
    })
  }

  selectTipoSolicitud(value: number) {
    this.tipoSolicitud = value;
    this.store.dispatch(listarPreguntaSolicitud({ tipoSolicitud: this.tipoSolicitud, idSolicitud: 0 }));
  }
  s
  cargarSolicitudEdicion() {
    this.blockUI.start("Cargando Solicitud...");
    this.solicitudService.findById(this.idSolicitud).subscribe(result => {
      if (result != null) {
        this.blockUI.stop();
        this.solicitud = result;
        if (!result.isEdit || this.accion != "aprobEdit") {
          this.router.navigate([`home`]);
        } else {
          this.store.dispatch(listarPreguntaSolicitud({ tipoSolicitud: result.tipoSolicitud.id, idSolicitud: this.solicitud.id }));
        }
      }
    })
  }

  cargarSolicitudDetalle() {
    this.blockUI.start("Cargando Detalle...");
    this.tipoSolicitud = this.solicitud.tipoSolicitud.id;
    this.detalleService.getRespuestasBySolicitud(this.solicitud).subscribe(result => {
      if (result != null) {
        this.blockUI.stop();
        this.autocompletarSolicitud(result);
        this.autocompletarDocumentacion(result);
      }
    })
  }

  findAprobador() {
    if (!this.isEditSolicitud) {
      this.aprobadorService.findUsuarioSolicitud().subscribe(result => {
        // console.log(result);
        if (result == null) {
          this.puedeEnviarSolicitud = false;
          this.alert.toastError("No tiene el cargo para realizar solicitudes");
        } else {
          this.tipoAprobador = result.tipoAprobador.id;
          this.tipoAprobadordesc = result.tipoAprobador.nombre;
        }
      })
    }
  }

  //* FUNCION QUE INICIA EL PROCESO DE CREACIÓN DE SOLICITUD
  //* ----------------------------------------------------------------------------------

  crearFormularioTask(preguntaRespuestas: PreguntaRespuestaModel[]): void {
    preguntaRespuestas.forEach(pregunta => {
      let respuestaTask: SubTask[] = [];

      pregunta.respuestas.forEach(respuesta => {
        let respuestaTemp: SubTask = {
          id: respuesta.idRespuesta,
          name: respuesta.descripcion,
          completed: false,
          color: 'primary',
          isText: respuesta.contieneTexto ? true : false,
          valueText: ''
        }
        respuestaTask.push(respuestaTemp);
      });

      let preguntaTemp: Task = {
        id: pregunta.idPregunta,
        number: preguntaRespuestas.indexOf(pregunta) + 1,
        name: pregunta.descripcion,
        completed: false,
        subtasks: respuestaTask
      }
      this.taskSolicitud.push(preguntaTemp);
    })
  }

  public volver(): void {
    this.router.navigate([`home`]);
  }


  //* METODOS PARA VALIDAR QUE EL FORMULARIO ESTA COMPLETADO
  //* ----------------------------------------------------------------------------------

  public continuarSolicitud(stepper: MatStepper): void {
    this.taskSolicitud.forEach(task => {
      task.completed = task.subtasks.every(t => t.completed) ? true : false;
    })
    if (this.taskSolicitud.every(t => t.completed)) {
      this.alert.toastInfo("Formulario Valido");
      this.botonTitle = "Continuar";
      this.taskCompleted = true;
      stepper.next();
    } else {
      this.alert.toastError("Debe completar todo el formulario");
      this.taskCompleted = false;
    }
  }

  public completedText(subtask: SubTask, pregunta: Task) {
    if (subtask.valueText != '') {
      pregunta.subtasks.filter(s => s.name == subtask.name)[0].completed = true;
    } else {
      pregunta.subtasks.filter(s => s.name == subtask.name)[0].completed = false;
    }
  }

  //* FUNCION QUE AGREGA Y ELIMINA LA DOCUMENTACION 
  //* ----------------------------------------------------------------------------------

  changeListener(files: FileList) {
    if (files && files.length > 0) {
      if (files.length > 3 || this.solicitudDocumentacion.length + files.length > 3) {
        this.alert.toastError("Solo se pueden adjuntar hasta 3 documentos");
      }
      else {
        for (let index = 0; index < files.length; index++) {
          let file: File = files.item(index);
          let documentacion = new DocumentosModel();
          this.fileToBase64(file).then(result => {
            documentacion.base64 = result;
          })
          documentacion.file = file;
          documentacion.nombre = file.name;
          documentacion.tipo = file.type;
          this.solicitudDocumentacion.push(documentacion);
        }
      }
    }
  }

  public popFile(file: DocumentosModel): void {
    this.solicitudDocumentacion = this.solicitudDocumentacion.filter(s => s != file);
  }

  deleteFile(documento: DocumentosModel): void {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Su documentacion sera eliminada y el aprobador no podra visualizarla.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.detalleService.eliminarDocumentacion(documento).subscribe(result => {
          if (result != null) {
            this.solicitudDocumentacion = this.solicitudDocumentacion.filter(d => d.id != documento.id)
          }
        })
      }
    })
  }


  //* FUNCION QUE TRANSFORMA DE FILE A BASE64
  //* ----------------------------------------------------------------------------------

  private fileToBase64 = (file: File): Promise<string> => {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result.toString());
      reader.onerror = error => reject(error);
    })
  }

  private Base64toFile(base64: string, nombre: string, tipo: string) {
    let arr = base64.split(','),
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], nombre, { type: tipo });
  }

  downloadFile(data: File) {
    const blob = new Blob([data], { type: data.type });
    // console.log(blob);
    const url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  //* METODO MARA AUTOCOMPLETAR SOLICITUD SI SE ESTA EDITANDO
  //* ----------------------------------------------------------------------------------

  private autocompletarSolicitud(solicitudDetalle: SolicitudDetalleRespuestas): void {
    solicitudDetalle.solicitudDetalle.forEach(a => {
      let subTaskSolicitud: SubTask = this.taskSolicitud.filter(t => t.id == a.pregunta.id)[0]?.subtasks.filter(st => st.id == a.respuesta.id)[0];
      if (subTaskSolicitud != undefined) {
        subTaskSolicitud.completed = true;
        if (a.otro != null && subTaskSolicitud.isText) {
          subTaskSolicitud.valueText = a.otro;
        }
      }
      subTaskSolicitud = null;
    })
  }

  private autocompletarDocumentacion(soliicitudDetalle: SolicitudDetalleRespuestas): void {
    soliicitudDetalle.solicitudDocumentacion.forEach(documento => {
      let doc = { ...documento };
      doc.file = this.Base64toFile(doc.base64, doc.nombre, doc.tipo);
      if (doc.nombre != "Formulario Solicitud") {
        this.solicitudDocumentacion.push(doc);
      }
    })
  }

  //* METODO MARA GUARDAR Y ENVIAR LA SOLICITUD
  //* ----------------------------------------------------------------------------------

  public guardarSolicitud(isBorrador: boolean): void {

    if (this.solicitudDocumentacion.length == 0 && !isBorrador) {
      this.alert.toastError("Debe adjuntar la documentación requerida para continuar con el envio de la solicitud.");
    } else {
      this.blockUI.start("Generando solicitud...");
      let alumnoSolicitudTemp = { ... this.alumnoSolicitud };
      // console.log(this.solicitud);
      alumnoSolicitudTemp.respuestaSolicitud = this.taskSolicitud;
      alumnoSolicitudTemp.isEdit = isBorrador;
      alumnoSolicitudTemp.idSolicitud = this.solicitud.id != null ? this.solicitud.id : 0;
      alumnoSolicitudTemp.solicitudDocumentacion = this.solicitudDocumentacion;
      alumnoSolicitudTemp.carreraInnovacion = this.taskSolicitud[0].subtasks[1].valueText;
      alumnoSolicitudTemp.facultadInnovacion = this.taskSolicitud[0].subtasks[0].valueText;
      if (this.solicitud.id != null && (this.solicitud.estadoSolicitud.id == 2 && this.solicitud.isEdit)) {
        alumnoSolicitudTemp.notificar = false;
        alumnoSolicitudTemp.edicionPostEnvio = true;
      }

      this.solicitudService.crearSolicitudInnovacion(alumnoSolicitudTemp, this.tipoSolicitud).subscribe(result => {
        if (result != null) {
          this.blockUI.stop();
          this.alert.successSwal("Solicitud creada correctamente");
          this.router.navigate([`/aerc/solicitudes-academicas/listado-solicitudes`]);
        }
      }, (_error) => {
        this.blockUI.stop();
        this.alert.errorSwal("Ha ocurrido un error al momento de guardar su solicitud");
      });
    }


  }

  public guardarBorrador(): void {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Su solicitud se guardara como borrador.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.guardarSolicitud(true);
      }
    })
  }
}

