import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SolicitudAcademicaComponent } from './solicitud-academica.component';

const routes: Routes = [
  { path: '', component: SolicitudAcademicaComponent },
  { path: ':solicitud/:accion', component: SolicitudAcademicaComponent }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SolicitudAcademicaRoutingModule { }
